#spinner {
  visibility: visible;
  width: 80px;
  height: 80px;

  border: 2px solid #e91515;
  border-top: 3px solid #393d3f;
  border-radius: 100%;

  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;

  animation: spin 1s infinite linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#spinner.hide {
  visibility: hidden;
}

